.header {
  background-color: #b91c1c; /* red-700 */
  color: white;
  padding: 0.7rem; /* Reduced padding for smaller height */
  text-align: left;
  font-size: 20pt;
  height: 40px;
}


#header_row{
  display: flex;
  justify-content: space-between;
  align-items: center;
}

#header_item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  
}

