body {
  margin: 0;
  font-family: 'Roboto', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2e2e2e
}

p{
  padding: 3px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

button#login{
  height: 40px;
  width: 80px;
    font-weight: 600;
  border-radius: 6px;
  font-size: 12pt;
  margin: 2px;
  border: 0px;
  border-radius: 2px;
}

input#Buscar {
  border-radius: 6px;
  height: 35px;
  margin: 2px;
  border: 0px;  
  padding-left: 10px;
  background-color: #f4f4f4;
}


div.menu_input{
  display: inline-block;
}

div.menu_input_section{
  display: flex;
  align-items: center;  
  justify-content: space-evenly;
  
}

ul li {
  margin-right: 20px;
  padding: 2px
}


strong {
  width: '100%';
}